.container{margin:0 1em;padding:0.5em 1em;background:#fff;border-radius:2px;white-space:nowrap;flex:1;position:relative;box-shadow:rgba(50,50,93,0.1) 0 7.5px 17.5px,rgba(0,0,0,0.07) 0 2.5px 7.5px;transition-property:transform,box-shadow;transition-duration:.44s}.container:hover{cursor:pointer;transform:translateY(-2px);box-shadow:rgba(50,50,93,0.1) 0 9px 17.5px,rgba(0,0,0,0.07) 0 4px 7.5px}.header::after{content:'';display:block;height:1px;background-image:linear-gradient(90deg, #070707, transparent)}.icon{opacity:0;font-size:1.2em;right:0.1em;top:0;position:absolute;transition:opacity 0.22s}.text{font-variant:small-caps;font-size:0.9em;line-height:1.25}.stat{display:block;margin:0.5em 0 0.25em;padding:0 0.25em;letter-spacing:-1px;text-align:center;font:600 0.95em / 100% monospace}@media only screen and (min-width: 999px){.stat{margin:0.6em 0 0.3em;font-size:1.05em;padding:0 1.25em}}@media only screen and (min-width: 1331px){.stat{margin:0.7em 0 0.35em;font-size:1.15em;padding:0 2.25em}}@media only screen and (min-width: 1663px){.stat{margin:0.8em 0 0.4em;font-size:1.25em;padding:0 3.25em}}



